body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

section {
  padding-top: 5rem;
}

.list-group-item.active {
  z-index: 2;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.list-group-item {
  cursor: pointer;
}

.list-group-item:hover {
  cursor: pointer;
  font-weight: 700;
}

.nav-link {
  font-size: 1.3rem;
}

@media (max-width: 480px) {
  .mobile_fix {
    width: 80% !important;
  }

  .logo-mobile {
    order: 3;
    padding: 2rem 0rem;
    margin-bottom: -3rem;
  }

  .nav-link {
    font-size: 1.5rem;
  }

  .list-group-item {
    border: 0px;
  }
}

.cursor-pointer {
  cursor: pointer;
}
.cursor-pointer:hover {
  color: #0056b3 !important;
}

.remove:hover {
  color: red !important;
}
